
.datetime-picker input {
  height: 4em !important;
}
.custom-tabs{
  background-color: #fff;
  border-top: 2px solid #f8f8f8;
}
.nav-tabs{
  width: 40%;
  margin: 0 auto;
  background-color: #fff;
  border-bottom: 0;
}
.nav-tabs>li{
  width: 25%;
  text-align: center;
  font-size: 17px;
}
.nav-tabs>li>a{
  border: 0;
  color: #444;
}
.nav-tabs>li>a{
  border: 0;
  color: #b2b2b2;
}
.nav-tabs>li>a:hover{
  background: transparent
}
.nav-tabs>li>a:hover,.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover{
  border: 0;
  border-bottom: 2px solid #002A3A;
  color: #002A3A;
}
.tab-content{
  background-color: #fff;
  padding: 5px 20px;
  border-radius: 5px;
}



