
.user-panel {
  height: 10em;
  border-bottom:2px solid #f8f8f8;
  border-top:2px solid #f8f8f8;
}
.user-panel h4 {
  color: #b2b2b2;
  margin-top: 0;
  margin-bottom: 0;
}
.status {
  min-height: 7em;
  border-bottom: 2px solid #f8f8f8;
  margin-bottom: 10px;
  padding: 5px 10px 10px;
}
.status h5 {
  color: #b2b2b2;
  margin-bottom: 0;
}
.status h5 p{
  color: #333;
}
.status p {
  margin-bottom: 0;
  font-size: 16px;
}
.status .fa.text-yellow {
  font-size: 10px;
  vertical-align: middle;
  margin-left: 5px;
}
.status .domain-icon-link{
  display: none;
}
p.first-color{
 color: #002A3A;
 margin-bottom: 0;
}
p.second-color{
  color: #b2b2b2;
 margin-bottom: 0;
}
.pie-chart{
  float: right;
}
.chart-desc{
  margin-top: 50px;
}



